import { ReactNode } from 'react'

interface ListProps<T> {
  items: T[],
  render: (item: T) => ReactNode,
}

// <T>   ,  ','   .    .
const List = <T,>({items, render}: ListProps<T>) => {
  return (
    <div>List
      <ul>
        {items.map((item, i) => (
          <li key={i}>
            {render(item)}
          </li>
        ))}
      </ul>
    </div>
  )
}

export default List